import './FabricanteProductoUpdate.css'

import { IonBadge, IonAlert, IonModal, IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'

import { AdminItemField, Page, PhotoLoaderModalRegistro, SelectorModal, VideoLoaderModal } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP_FABRICANTE, TIPOS_REFRIGERACION_SELECT } from '../../constants'
import { cambiar_formato_numero, cantidad_fmt } from '../../helpers'
import { comprimirImagen, DetailViewProps, useToaster, useTryGoBack } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { ImagenProducto, Producto } from '../../services/api'
import { Capacitor } from '@capacitor/core'
import { Filesystem } from '@capacitor/filesystem'
import Uppy from '@uppy/core'
import { DashboardModal } from '@uppy/react'
import Webcam from '@uppy/webcam'
import moment from 'moment'
import { DATE_FORMAT } from '../../utils/date'

const Espanol = require('@uppy/locales/lib/es_ES')
const ImageCompressor = require('uppy-plugin-image-compressor')
const numeral = require('numeral')

interface ImagenSeleccionada {
  name: string
  file: File | Blob
}

export const FabricanteProductoUpdate: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Producto, setProducto] = useState({} as any)
  const [Errors, setErrors] = useState<Producto | null>(null)
  const IdProducto = match.params ? match.params.id : null
  const ProductoRef = useRef(null as any)
  const [Loading, setLoading] = useState(true)
  const [Editando, setEditando] = useState(false)
  const [Alert, setAlert] = useState(false)
  const [TituloAlert, setTituloAlert] = useState('')
  const [TextoAlert, setTextoAlert] = useState('')
  const [VisibleUppy, setVisibleUppy] = useState(false)
  const [ModalInfo, setModalInfo] = useState<string | null>(null)
  const [FuncionAlert, setFuncionAlert] = useState('')
  const addToast = useToaster()
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const [FotoPrincipal, setFotoPrincipal] = useState(null as any)
  const [FotoPrincipalPreview, setFotoPrincipalPreview] = useState(null as any)
  const [Imagenes, setImagenes] = useState<ImagenSeleccionada[]>([])
  const ImagenSeleccionada = useRef<ImagenProducto>(null as any)
  const [CantidadReponer, setCantidadReponer] = useState('')
  const [CantidadReponerProgramado, setCantidadReponerProgramado] = useState('')
  const [FechaReposicion, setFechaReposicion] = useState('')
  const tryGoBack = useTryGoBack()
  const [buttonDisabled, setButtonDisabled] = useState(false)

  const setup = async () => {
    if (IdProducto) {
      obtenerProducto(parseInt(IdProducto))
    } else {
      setEditando(true)
      setLoading(false)
    }
  }

  useEffect(() => {
    setup()
  }, [])

  const obtenerProducto = (idProducto: number) => {
    rootStore.environment.api.getProductoAdmin(idProducto).then((result) => {
      if (result.kind === 'ok') {
        setProducto(result.data)
        ProductoRef.current = result.data
        //
        if (result.data.imagenes.length) {
          const portada = result.data.imagenes.find((img: ImagenProducto) => img.portada)
          if (portada) {
            setFotoPrincipalPreview(portada.imagen)
          }
        }
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
      setLoading(false)
    })
  }

  const setDato = (campo: string, valor: string) => {
    let datos = { ...ProductoRef.current }
    // @ts-ignore
    datos[campo] = valor
    setProducto(datos as any)
    ProductoRef.current = datos
  }

  const setDatos = (Datos: any) => {
    let datos = { ...ProductoRef.current, ...Datos }
    setProducto(datos as any)
    ProductoRef.current = datos
  }

  const dataSelectorModal = (campo: string, data: any) => {
    setDato(campo, data[0])
  }

  const setDatoInput = (campo: string, data: any) => {
    if (campo === 'descripcion') {
      data = data.substring(0, 400)
    }
    setDato(campo, data)
  }

  const guardar = () => {
    if (!FotoPrincipal && !IdProducto) {
      addToast('Seleccione la foto principal para continuar', 'warning')
      return
    }
    setButtonDisabled(true)
    setErrors({} as any)
    setLoading(true)

    const categoria =
      Producto.categoria && typeof Producto.categoria === 'object' ? Producto.categoria.id : Producto.categoria
    const familia = Producto.familia && typeof Producto.familia === 'object' ? Producto.familia.id : Producto.familia
    const linea = Producto.linea && typeof Producto.linea === 'object' ? Producto.linea.id : Producto.linea
    let datos = {
      ...Producto,
      categoria: categoria || '',
      familia: familia || '',
      linea: linea || '',
      nombre: Producto.nombre,
      descripcion: Producto.descripcion,
      descripcion_extensa: Producto.descripcion_extensa,
      tipo: 'detalle',
    }
    delete datos.imagenes
    if (datos.peso_kg) {
      datos = { ...datos, peso_kg: cambiar_formato_numero(datos.peso_kg) }
    }
    if (datos.alto_cm) {
      datos = { ...datos, alto_cm: cambiar_formato_numero(datos.alto_cm) }
    }
    if (datos.ancho_cm) {
      datos = { ...datos, ancho_cm: cambiar_formato_numero(datos.ancho_cm) }
    }
    if (datos.profundidad_cm) {
      datos = { ...datos, profundidad_cm: cambiar_formato_numero(datos.profundidad_cm) }
    }
    if (datos.descripcion_extensa === undefined) {
      datos.descripcion_extensa = ''
    }

    rootStore.environment.api.guardarProducto(datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        guardarFotos(result.response)
      } else if (result.kind === 'bad-data') {
        addToast('Corrija los errores de validación y vuelva a intentar', 'warning')
        setErrors(result.errors)
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
        setButtonDisabled(false)
      } else {
        addToast('Ocurrió un error al guardar, vuelva a intentar en unos momentos', 'error')
        setButtonDisabled(false)
      }
    })
  }

  const guardarFotos = async (producto: any) => {
    let imgs = []
    if (FotoPrincipal) {
      imgs.push({ imagen: FotoPrincipal, portada: true })
    }
    imgs = imgs.concat(
      Imagenes.map((img) => {
        return { imagen: img.file, portada: false }
      }) as any,
    )
    for (let i = 0; i < imgs.length; i++) {
      const img = imgs[i]
      await rootStore.environment.api.postMultipart(
        '/imagen_producto/',
        {
          producto: producto.id,
          ...img,
        },
        ['imagen'],
      )
    }
    //
    if (IdProducto) {
      obtenerProducto(parseInt(IdProducto))
    }
    //
    addToast('Producto guardado exitosamente', 'success')
    // Reset
    setEditando(false)
    setImagenes([])
    setFotoPrincipal(null)
    setButtonDisabled(false)
    //
    history.replace(RUTAS_APP_FABRICANTE.PRODUCTO_UPDATE(producto.id))
  }

  const videoSelected = async (data: any) => {
    if (data === null) {
      setDatos({
        delete_video: true,
        video: null,
      })
      return
    }
    let blob = await fetch(data).then((r) => r.blob())
    setDatos({
      delete_video: false,
      video: blob,
    })
  }

  const imgPrincipalSelected = async (data: any, tipo: string) => {
    console.log('imgSelected', data)
    if (Capacitor.getPlatform() !== 'web') {
      const contents = await Filesystem.readFile({
        path: data,
      })
      data = `data:image/jpeg;base64,${contents.data}`
    }
    //
    setFotoPrincipalPreview(data)
    //
    let blob = await fetch(data).then((r) => r.blob())
    const imagen_comprimida = await comprimirImagen(blob, tipo, () => {})
    setFotoPrincipal(imagen_comprimida)
  }

  const seleccionarFotosSecundarias = () => {
    setVisibleUppy(true)
  }

  const uppy = React.useMemo(() => {
    return Uppy({
      restrictions: { allowedFileTypes: ['image/*'] },
      meta: {
        producto: IdProducto,
      },
      locale: Espanol,
    })
      .use(Webcam, {
        videoConstraints: {
          facingMode: 'environment',
        },
      })
      .use(ImageCompressor, {
        maxHeight: 2000,
        maxWidth: 2000,
        quality: 0.8,
      })
  }, [IdProducto])

  React.useEffect(() => {
    uppy.on('complete', (result) => {
      const imagenes = [...Imagenes]
      for (let i = 0; i < result.successful.length; i++) {
        let uppyImage = result.successful[i]
        imagenes.push({
          name: uppyImage.name,
          file: uppyImage.data,
        })
      }
      setImagenes(imagenes)
      setVisibleUppy(false)
    })
    return () => uppy.close()
  }, [])

  const eliminarFoto = (idx: number) => {
    const imagenes = [...Imagenes]
    imagenes.splice(idx, 1)
    setImagenes(imagenes)
  }

  const guardarClick = () => {
    if (Loading) {
      return
    }
    if (!IdProducto) {
      guardar()
      return
    }
    setAlert(true)
    setFuncionAlert('guardar')
    setTituloAlert('Atención')
    if (Producto && Producto.id) {
      setTextoAlert('Al editar el producto pasará a estar pendiente de aprobación por parte del staff de ABi')
    } else {
      setTextoAlert('Al crear un producto el mismo estará pendiente de aprobación por parte del staff de ABi')
    }
  }

  const imagenClick = (imagen: ImagenProducto) => {
    ImagenSeleccionada.current = imagen
    setTituloAlert('Modificar imagen')
    setTextoAlert('Qué desea hacer con la imagen?')
    setFuncionAlert('imagen')
    setAlert(true)
  }

  const botonesAlert = () => {
    const btnCancelar = {
      text: 'Cancelar',
      role: 'cancel',
      cssClass: 'btn-secondary',
    }
    let botones = [btnCancelar, { text: 'Confirmar', handler: guardar }]
    if (FuncionAlert === 'imagen') {
      botones = [
        btnCancelar,
        { text: 'Eliminar', handler: eliminarImagen },
        { text: 'Hacer principal', handler: setImagenPrincipal },
      ]
    }
    return botones
  }

  const setImagenPrincipal = () => {
    const imagen_id = ImagenSeleccionada.current.id
    rootStore.environment.api.seleccionarImagenPortada(imagen_id).then((result: any) => {
      if (result.kind === 'ok') {
        const imagenPortada = Producto.imagenes.find((imagen: ImagenProducto) => imagen.id === imagen_id)
        setFotoPrincipalPreview(imagenPortada.imagen)
        addToast('Imagen de portada actualizada satisfactoriamente', 'success')
      } else {
        addToast('Ocurrió un error al actualizar la imagen de portada, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const eliminarImagen = () => {
    if (Imagenes.length < 2) {
      addToast('No se puede eliminar la foto principal del producto', 'warning')
      return
    }
    const imagen_id = ImagenSeleccionada.current.id
    rootStore.environment.api.eliminarImagen(imagen_id).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Imagen eliminada satisfactoriamente', 'success')
        const producto = { ...Producto }
        const imagenEliminar = producto.imagenes.find((imagen: ImagenProducto) => imagen.id === imagen_id)
        if (imagenEliminar) {
          producto.imagenes.splice(producto.imagenes.indexOf(imagenEliminar), 1)
          setProducto(producto)
        }
      } else {
        addToast('Ocurrió un error al eliminar, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const eliminar = () => {
    if (!window.confirm('Confirma que desea discontinuar el producto?')) {
      return
    }
    setLoading(true)
    rootStore.environment.api.eliminarProducto(IdProducto).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Producto discontinuado exitosamente', 'success')
        history.replace(RUTAS_APP_FABRICANTE.CATALOGO)
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const desbloquear = () => {
    if (!window.confirm('Confirma que desea mostrar el producto en la tienda?')) {
      return
    }
    setErrors({} as any)
    setLoading(true)

    const categoria =
      Producto.categoria && typeof Producto.categoria === 'object' ? Producto.categoria.id : Producto.categoria
    const familia = Producto.familia && typeof Producto.familia === 'object' ? Producto.familia.id : Producto.familia
    const linea = Producto.linea && typeof Producto.linea === 'object' ? Producto.linea.id : Producto.linea
    let datos = {
      ...Producto,
      categoria: categoria || '',
      familia: familia || '',
      linea: linea || '',
      nombre: Producto.nombre,
      descripcion: Producto.descripcion,
      descripcion_extensa: Producto.descripcion_extensa,
      tipo: 'detalle',
    }
    delete datos.imagenes
    if (datos.peso_kg) {
      datos = { ...datos, peso_kg: cambiar_formato_numero(datos.peso_kg) }
    }
    if (datos.alto_cm) {
      datos = { ...datos, alto_cm: cambiar_formato_numero(datos.alto_cm) }
    }
    if (datos.ancho_cm) {
      datos = { ...datos, ancho_cm: cambiar_formato_numero(datos.ancho_cm) }
    }
    if (datos.profundidad_cm) {
      datos = { ...datos, profundidad_cm: cambiar_formato_numero(datos.profundidad_cm) }
    }
    if (datos.bloqueado) {
      datos = { ...datos, bloqueado: false }
    }
    console.log(datos)
    rootStore.environment.api.guardarProducto(datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        guardarFotos(result.response)
      } else if (result.kind === 'bad-data') {
        addToast('Corrija los errores de validación y vuelva a intentar', 'warning')
        setErrors(result.errors)
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        addToast('Ocurrió un error al guardar, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const reponerClick = () => {
    setModalInfo('reponer')
  }

  const programarReposicionClick = () => {
    setModalInfo('programarReposicion')
  }

  const guardarReponerProgramado = () => {
    const cantidad = parseInt(CantidadReponerProgramado)
    if (isNaN(cantidad) || cantidad < 1) {
      addToast('Ingrese un número entero sin separador de miles', 'warning')
      return
    }
    // validar fecha
    const fecha = moment(FechaReposicion, 'DD/MM/YYYY')
    const fecha_ahora = moment()
    let valido = fecha.isValid() ? true : false
    if (!valido || !(fecha > fecha_ahora)) {
      addToast('Ingrese una fecha de reposición válida', 'warning')
      return
    }
    let datos = {
      id: IdProducto,
      cantidad_reposicion: CantidadReponerProgramado,
      fecha_reposicion: fecha.format('YYYY-MM-DD'),
      tipo: 'inventario_programado',
      precio: Producto.precio,
      precio_mayorista: Producto.precio_mayorista,
    }
    const product = {
      ...Producto,
      ...datos,
    }
    rootStore.environment.api.guardarProducto(datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setProducto(product)
        setCantidadReponerProgramado('')
        setFechaReposicion('')
        addToast('Reposición programada exitosamente', 'success')
        setModalInfo(null)
      } else if (result.kind === 'bad-data') {
        addToast('Corrija los errores de validación y vuelva a intentar', 'warning')
        setErrors(result.errors)
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        addToast('Ocurrió un error al guardar, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const eliminarReponerProgramado = () => {
    if (!window.confirm('Confirma que desea eliminar reposición?')) {
      return
    }
    let datos = {
      id: IdProducto,
      cantidad_reposicion: '',
      fecha_reposicion: '',
      tipo: 'inventario_programado',
      precio: Producto.precio,
      precio_mayorista: Producto.precio_mayorista,
    }
    const product = {
      ...Producto,
      ...datos,
    }
    rootStore.environment.api.guardarProducto(datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setProducto(product)
        setCantidadReponerProgramado('')
        setFechaReposicion('')
        addToast('Se eliminó la reposición', 'success')
        setModalInfo(null)
      } else if (result.kind === 'bad-data') {
        addToast('Corrija los errores de validación y vuelva a intentar', 'warning')
        setErrors(result.errors)
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        addToast('Ocurrió un error al guardar, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const guardarReponer = () => {
    const cantidad = parseInt(CantidadReponer)
    if (isNaN(cantidad) || cantidad < 1) {
      addToast('Ingrese un número entero sin separador de miles', 'warning')
      return
    }
    const cantidad_disponible = Producto.cantidad_disponible + CantidadReponer
    let datos = {
      id: IdProducto,
      cantidad_disponible,
      tipo: 'inventario',
    }
    rootStore.environment.api.guardarProducto(datos).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        setCantidadReponer('')
        setDato('cantidad_disponible', cantidad_disponible)
        addToast('Stock actualizado exitosamente', 'success')
        setModalInfo(null)
      } else if (result.kind === 'bad-data') {
        addToast('Corrija los errores de validación y vuelva a intentar', 'warning')
        setErrors(result.errors)
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        addToast('Ocurrió un error al guardar, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const descripcionLength = Producto.descripcion ? Producto.descripcion.length : 0
  const volumen =
    numeral(Producto.ancho_cm).value() * numeral(Producto.alto_cm).value() * numeral(Producto.profundidad_cm).value()

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'fabricante-producto-update'}>
      <SubHeader
        noContainer
        links={[
          { titulo: 'Productos', callback: () => tryGoBack(RUTAS_APP_FABRICANTE.CATALOGO) },
          { titulo: Producto && Producto.id ? 'Editar producto' : 'Crear producto' },
        ]}
      />
      {Producto.bloqueado && (
        <div className="info-retirado">
          <FIcon.AlertCircle color="white" size={20} className="mr-2" />
          SIN STOCK. Retirado de Tienda ABi
        </div>
      )}

      <DashboardModal
        uppy={uppy}
        closeModalOnClickOutside
        open={VisibleUppy}
        onRequestClose={() => {
          setVisibleUppy(false)
          uppy.reset()
        }}
        plugins={['Webcam', 'ImageEditor']}
        metaFields={[{ id: 'name', name: 'Nombre', placeholder: 'Nombre del archivo' }]}
      />

      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TituloAlert}
        message={TextoAlert}
        buttons={botonesAlert()}
      />

      {ModalInfo !== null && (
        <IonModal isOpen={true} cssClass={`modal-info-${ModalInfo}`} onDidDismiss={() => setModalInfo(null)}>
          {ModalInfo === 'fotos' && (
            <>
              <div className="heather-info-registro background-color-fabricante">
                <div>
                  <span>Fotos de productos</span>
                </div>

                <div onClick={() => setModalInfo(null)}>
                  <FIcon.XCircle style={{ width: 26, marginRight: 10, color: '#fff' }} />
                </div>
              </div>
              <div
                className="sub-titulo-login-abi"
                style={{ height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}
              >
                <div style={{ textAlign: 'initial', padding: 10, fontSize: '1rem' }}>
                  <p>
                    Las fotos de productos deben cumplir con los requerimientos de imágenes publicatarias de ABi. Podes
                    leer los requerimientos haciendo click{' '}
                    <a href="https://app.abi.com.py/media/requisitos-imagenes-abi.pdf" target={'_blank'}>
                      aquí
                    </a>
                    .
                  </p>
                </div>
              </div>
            </>
          )}
          {ModalInfo === 'dimensiones' && (
            <>
              <div className="heather-info-registro background-color-fabricante">
                <div>
                  <span>Dimensiones</span>
                </div>

                <div onClick={() => setModalInfo(null)}>
                  <FIcon.XCircle style={{ width: 26, marginRight: 10, color: '#fff' }} />
                </div>
              </div>
              <div
                className="sub-titulo-login-abi"
                style={{ height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}
              >
                <div style={{ textAlign: 'initial', padding: 10, fontSize: '1rem' }}>
                  <p>ABi asigna los pedidos al driver en función de las capacidades de carga del rodado que utiliza</p>
                </div>
              </div>
            </>
          )}
          {ModalInfo === 'reponer' && (
            <>
              <div className="heather-info-registro background-color-fabricante">
                <div>
                  <span>Reponer</span>
                </div>

                <div onClick={() => setModalInfo(null)}>
                  <FIcon.XCircle style={{ width: 26, marginRight: 10, color: '#fff' }} />
                </div>
              </div>
              <div
                className="sub-titulo-login-abi"
                style={{ height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}
              >
                <div style={{ textAlign: 'initial', padding: 10, fontSize: '1rem' }}>
                  <p>Ingrese la cantidad que desea adicionar al stock</p>
                  <div className="row">
                    <AdminItemField
                      titulo={''}
                      tipo={'numero'}
                      campo={''}
                      valor={CantidadReponer}
                      set_dato={(campo: string, valor: string) => setCantidadReponer(valor)}
                      columnas={12}
                      error={Errors && Errors.cantidad_disponible}
                    />
                  </div>
                  <button className="btn btn-primary" onClick={guardarReponer}>
                    Aceptar
                  </button>
                </div>
              </div>
            </>
          )}
          {ModalInfo === 'programarReposicion' && (
            <>
              <div className="heather-info-registro background-color-fabricante">
                <div>
                  <span>Programar reposición</span>
                </div>
                <div onClick={() => setModalInfo(null)}>
                  <FIcon.XCircle style={{ width: 26, marginRight: 10, color: '#fff' }} />
                </div>
              </div>
              <div
                className="sub-titulo-login-abi"
                style={{ height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}
              >
                <div style={{ textAlign: 'initial', padding: 10, fontSize: '1rem' }}>
                  <p>Ingrese la cantidad que desea adicionar al stock</p>
                  <div className="row">
                    <AdminItemField
                      titulo={''}
                      tipo={'numero'}
                      campo={''}
                      valor={CantidadReponerProgramado}
                      set_dato={(campo: string, valor: string) => setCantidadReponerProgramado(valor)}
                      columnas={12}
                      error={Errors && Errors.cantidad_disponible}
                    />
                  </div>
                  <p>Ingrese la fecha</p>
                  <div className="row">
                    <AdminItemField
                      titulo={''}
                      tipo={'fecha'}
                      campo={''}
                      valor={FechaReposicion}
                      set_dato={(campo: string, valor: string) => setFechaReposicion(valor)}
                      columnas={12}
                    />
                  </div>
                  <button className="btn btn-primary" onClick={guardarReponerProgramado}>
                    Aceptar
                  </button>
                </div>
              </div>
            </>
          )}
        </IonModal>
      )}

      {Loading && <IonSpinner name="crescent" className={'loadingSpinnerFabricanteApp pt-5'} />}

      {!Loading && (
        <div className={Movil ? 'container-lg' : ''}>
          {IdProducto && (
            <div className={Movil ? 'row' : 'd-flex flex-row pl-3 flex-grow-1'}>
              <div className={Movil ? 'col-12' : ''}>
                {!Producto.bloqueado && Producto && Producto.cantidad_disponible <= 5 && Producto.cantidad_disponible > 0 && (
                  <IonBadge style={{ backgroundColor: 'var(--naranja-abi)' }} className="alert-bagde">
                    <FIcon.AlertCircle color={'#FFF'} size={18} className="mr-2" />
                    Quedan pocas unidades
                  </IonBadge>
                )}
                {!Producto.bloqueado && Producto && Producto.cantidad_disponible == 0 && (
                    <IonBadge style={{ backgroundColor: 'rgb(225, 6, 0)' }} className="alert-bagde">
                      <FIcon.AlertCircle color={'#FFF'} size={18} className="mr-2" />
                      SIN STOCK. Retirado de Tienda ABi
                    </IonBadge>
                )}
                <PhotoLoaderModalRegistro
                  titulo_modal={'Subir foto principal'}
                  titulo_label={''}
                  helptext={''}
                  img_path={''}
                  on_select={imgPrincipalSelected}
                  userdata={'imagen_perfil'}
                  preview={FotoPrincipalPreview}
                  source={0}
                  editor={false}
                  perfil={true}
                  open_modal={false}
                />
              </div>

              <div className={Movil ? 'col-12' : 'd-flex flex-column pl-4 flex-grow-1 justify-content-center'}>
                <div className="d-flex flex-row align-items-center">
                  <input
                    type="text"
                    className="form-control input-nombre"
                    placeholder="Nombre del producto"
                    value={Producto ? Producto.nombre : ''}
                    onChange={(evt) => setDatoInput('nombre', evt.target.value)}
                    readOnly={!Editando}
                  />
                  {IdProducto && Producto.activo && <FIcon.CheckCircle color={'var(--success)'} size={35} />}
                  {IdProducto && !Producto.activo && <FIcon.XCircle color={'var(--danger)'} size={35} />}
                </div>
                {IdProducto && <div>{`Id#${Producto.id}`}</div>}
                {loginStore.fabricante && (
                  <div>
                    {loginStore.fabricante.rating}
                    <FIcon.Star size={'1rem'} color="var(--oro)" className="ml-1" />
                  </div>
                )}
              </div>
            </div>
          )}

          <div className={'row'}>
            {(!IdProducto || Editando) && (
              <div className="col-12 my-3">
                {FotoPrincipal && (
                  <div className="foto-seleccionada" onClick={() => setFotoPrincipal(null)}>
                    Foto principal
                    <FIcon.X color={'#4E4E4E'} size={18} className="float-right" />
                  </div>
                )}

                {Imagenes.map((img, idx) => (
                  <div key={idx} className="foto-seleccionada" onClick={() => eliminarFoto(idx)}>
                    {img.name}
                    <FIcon.X color={'#4E4E4E'} size={18} className="float-right" />
                  </div>
                ))}

                <div className="text-center text-lg-left botones-fotos-container">
                  <PhotoLoaderModalRegistro
                    titulo_modal={'Subir foto principal'}
                    titulo_label={'Subir foto principal'}
                    helptext={''}
                    on_select={imgPrincipalSelected}
                    userdata={'imagen_perfil'}
                    preview={''}
                    source={0}
                    editor={false}
                    perfil={false}
                    open_modal={false}
                  >
                    <button className="btn btn-primary px-5">
                      <FIcon.Camera color={'#FFF'} size={16} className="mr-2" />
                      Foto principal
                    </button>
                  </PhotoLoaderModalRegistro>
                  <button className="btn btn-primary px-4 ml-lg-3 mt-2 mt-lg-0" onClick={seleccionarFotosSecundarias}>
                    <FIcon.Camera color={'#FFF'} size={16} className="mr-2" />
                    Fotos secundarias
                  </button>
                  <span className="cpointer" onClick={() => setModalInfo('fotos')}>
                    <img src="assets/icon/info.png" width="38px" height="38px" />
                  </span>
                </div>
              </div>
            )}

            {!IdProducto && (
              <AdminItemField
                titulo={'Nombre del producto'}
                tipo={'texto'}
                campo={'nombre'}
                valor={Producto ? Producto.nombre : ''}
                error={Errors && Errors.nombre}
                set_dato={setDatoInput}
                columnas={12}
              />
            )}

            <AdminItemField
              titulo={'Precio normal (Gs.)'}
              tipo={'numero'}
              campo={'precio'}
              valor={Producto ? Producto.precio : ''}
              error={Errors && Errors.precio}
              set_dato={setDatoInput}
              columnas={4}
              deshabilitado={Editando} // Esta invertido :S
            />

            <AdminItemField
              titulo={'Precio mayorista (Gs.)'}
              tipo={'numero'}
              campo={'precio_mayorista'}
              valor={Producto ? Producto.precio_mayorista : ''}
              error={Errors && Errors.precio_mayorista}
              set_dato={setDatoInput}
              columnas={4}
              deshabilitado={Editando}
            />

            {!Producto.bloqueado && (
              <>
                <AdminItemField
                  titulo={'Cantidad mayorista'}
                  tipo={'numero'}
                  campo={'cantidad_mayorista'}
                  valor={Producto ? Producto.cantidad_mayorista : ''}
                  error={Errors && Errors.cantidad_mayorista}
                  set_dato={setDatoInput}
                  columnas={4}
                  deshabilitado={Editando}
                />

                <AdminItemField
                  titulo={'Descripción'}
                  tipo={'textarea'}
                  campo={'descripcion'}
                  valor={Producto ? Producto.descripcion : ''}
                  error={Errors && Errors.descripcion}
                  set_dato={setDatoInput}
                  columnas={12}
                  help_text={`${descripcionLength}/400`}
                  textAreaRows={3}
                  deshabilitado={Editando}
                />

                <div className={'col-12 col-lg-6 mt-2'}>
                  <SelectorModal
                    titulo={'Categoría'}
                    titulo_label={'Categoría'}
                    listado_elementos={rootStore.categorias}
                    listado_elementos_seleccionados={Producto && Producto.categoria ? Producto.categoria.id : null}
                    datos_filtrados={(data: any) => dataSelectorModal('categoria', data)}
                    multiple={false}
                    llave_label_opcion={'nombre'}
                    deshabilitado={Editando}
                  />
                  {Errors && Errors.categoria && <div className={'text-danger form-error'}>{Errors.categoria}</div>}
                </div>

                <div className={'col-12 col-lg-6 mt-2'}>
                  <SelectorModal
                    titulo={'Línea'}
                    titulo_label={'Línea'}
                    listado_elementos={rootStore.lineas}
                    listado_elementos_seleccionados={Producto && Producto.linea ? Producto.linea.id : null}
                    datos_filtrados={(data: any) => dataSelectorModal('linea', data)}
                    multiple={false}
                    llave_label_opcion={'nombre'}
                    deshabilitado={Editando}
                  />
                  {Errors && Errors.linea && <div className={'text-danger form-error'}>{Errors.linea}</div>}
                </div>

                <div className={'col-12 col-lg-6'}>
                  <SelectorModal
                    titulo={'Familia'}
                    titulo_label={'Familia'}
                    listado_elementos={rootStore.familias}
                    listado_elementos_seleccionados={Producto && Producto.familia ? Producto.familia.id : null}
                    datos_filtrados={(data: any) => dataSelectorModal('familia', data)}
                    multiple={false}
                    llave_label_opcion={'nombre'}
                    deshabilitado={Editando}
                  />
                  {Errors && Errors.familia && <div className={'text-danger form-error'}>{Errors.familia}</div>}
                </div>

                <AdminItemField
                  titulo={'Stock'}
                  tipo={'numero'}
                  campo={'cantidad_disponible'}
                  valor={Producto ? Producto.cantidad_disponible : ''}
                  error={Errors && Errors.cantidad_disponible}
                  set_dato={setDatoInput}
                  columnas={6}
                  extra_css="input-stock"
                  deshabilitado={Editando}
                />
              </>
            )}

            {!Producto.bloqueado && IdProducto && (
              <>
                <div className="col-12 mb-3">
                  <button
                    className="btn btn-primary mr-2"
                    onClick={() => {
                      if (Producto.promocion) {
                        history.push(RUTAS_APP_FABRICANTE.PROMOCION_UPDATE(IdProducto, Producto.promocion.id))
                      } else {
                        history.push(RUTAS_APP_FABRICANTE.PROMOCION_CREATE(IdProducto))
                      }
                    }}
                  >
                    {Producto.promocion ? 'Editar' : 'Crear'} promoción
                  </button>
                  <button className="btn btn-success px-4" onClick={reponerClick}>
                    Reponer
                  </button>
                </div>

                {Producto.promocion && (
                  <p className="col-12">
                    La promoción finaliza el: {moment(Producto.promocion.fecha_hasta).format(DATE_FORMAT.HDATETIME)}
                  </p>
                )}

                <div className="col-12">
                  {Producto.fecha_reposicion && (
                    <span className="px-2">
                      Reposición programada para el {moment(Producto.fecha_reposicion).format('DD/MM/YYYY')}, cantidad a
                      reponer: {cantidad_fmt(Producto.cantidad_reposicion)}
                      <button className="bg-white px-2" onClick={eliminarReponerProgramado}>
                        <FIcon.Trash className="text-danger ml-1 mb-2" style={{ width: 26, marginRight: 10 }} />
                      </button>
                      {/*<a></a>*/}
                    </span>
                  )}
                </div>
              </>
            )}

            <AdminItemField
              titulo={'Especificaciones'}
              tipo={'textarea'}
              campo={'descripcion_extensa'}
              valor={Producto ? Producto.descripcion_extensa : ''}
              error={Errors && Errors.descripcion_extensa}
              set_dato={setDatoInput}
              columnas={12}
              textAreaRows={5}
              deshabilitado={Editando}
            />

            <div className="col-12">
              <label className={'font-weight-bold'}>
                Dimensiones
                <span className="cpointer" onClick={() => setModalInfo('dimensiones')}>
                  <img src="assets/icon/info.png" width="30px" height="30px" />
                </span>
              </label>
            </div>

            <AdminItemField
              titulo={'Ancho (cm)'}
              tipo={'decimales'}
              campo={'ancho_cm'}
              valor={Producto ? Producto.ancho_cm : ''}
              error={Errors && Errors.ancho_cm}
              set_dato={setDatoInput}
              columnas={4}
              deshabilitado={Editando}
            />

            <AdminItemField
              titulo={'Alto (cm)'}
              tipo={'decimales'}
              campo={'alto_cm'}
              valor={Producto ? Producto.alto_cm : ''}
              error={Errors && Errors.alto_cm}
              set_dato={setDatoInput}
              columnas={4}
              deshabilitado={Editando}
            />

            <AdminItemField
              titulo={'Profundidad (cm)'}
              tipo={'decimales'}
              campo={'profundidad_cm'}
              valor={Producto ? Producto.profundidad_cm : ''}
              error={Errors && Errors.profundidad_cm}
              set_dato={setDatoInput}
              columnas={4}
              deshabilitado={Editando}
            />

            <AdminItemField
              titulo={'Peso (kg)'}
              tipo={'decimales'}
              campo={'peso_kg'}
              valor={Producto ? Producto.peso_kg : ''}
              error={Errors && Errors.peso_kg}
              set_dato={setDatoInput}
              columnas={4}
              deshabilitado={Editando}
            />

            <AdminItemField
              titulo={'Volumen (lts)'}
              tipo={'decimales'}
              campo={'volumen'}
              valor={volumen}
              set_dato={setDatoInput}
              columnas={4}
              deshabilitado={false}
            />

            <AdminItemField
              titulo={'Refrigeración'}
              tipo={'select2'}
              campo={'refrigeracion'}
              valor={Producto ? Producto.refrigeracion : 0}
              error={Errors && Errors.refrigeracion}
              set_dato={setDatoInput}
              columnas={4}
              opciones_select={TIPOS_REFRIGERACION_SELECT}
              deshabilitado={Editando}
            />

            {(Producto.video || Editando) && (
              <div className={'col-12 col-lg-6 mt-2'}>
                <label className={'font-weight-bold'}>Video del producto (opcional)</label>
                <VideoLoaderModal
                  titulo_modal={'Video del producto'}
                  titulo_label={'Seleccionar el video'}
                  on_select={videoSelected}
                  preview={Producto ? Producto.video : null}
                  disabled={!Editando}
                />
              </div>
            )}
            <div className="col-12 my-3 text-center text-lg-left">
              {!Producto.bloqueado && IdProducto && (
                <button className="btn btn-danger px-4" onClick={eliminar}>
                  Discontinuar
                </button>
              )}
              {!Producto.bloqueado && IdProducto && !Editando && (
                <button className="btn btn-primary px-5 ml-3" onClick={() => setEditando(true)}>
                  Editar
                </button>
              )}
              {(!IdProducto || Editando) && (
                <button className={'btn btn-primary px-5 ml-3'} disabled={buttonDisabled} onClick={guardarClick}>
                  Guardar
                </button>
              )}
              {!Producto.bloqueado && IdProducto && (
                <button className="btn btn-outline-primary px-4 ml-3 mt-2 mt-lg-0" onClick={programarReposicionClick}>
                  Programar reposición
                </button>
              )}
            </div>
            {IdProducto && Producto.imagenes && (
              <div className="col-12 mb-3 imagenes">
                {Producto.imagenes.map((imagen: ImagenProducto) => (
                  <div className="imagen" onClick={() => imagenClick(imagen)}>
                    <img src={imagen.imagen} />
                  </div>
                ))}
              </div>
            )}
            <div>
              {Producto.bloqueado && (
                <div className="d-flex flew-wrap" id="contenedor-btn-bloqueado">
                  <div className="ml-md-2 mr-md-2">
                    <button
                      className="btn px-4 ml-3 btn-bloqueado"
                      style={{ background: '#303ab2', color: '#fff' }}
                      onClick={programarReposicionClick}
                    >
                      Programar reposición
                    </button>
                  </div>
                  <div className="ml-md-2 mr-md-2 mt-2 mb-2 mt-md-0 mb-md-0">
                    <button
                      className="btn px-4 btn-bloqueado"
                      style={{ background: 'rgb(150,206,80)', color: '#fff' }}
                      onClick={reponerClick}
                    >
                      Reponer
                    </button>
                  </div>
                  <div className="ml-md-2 mr-md-2">
                    <button className="btn btn-danger px-4 btn-bloqueado" onClick={desbloquear}>
                      Discontinuado
                    </button>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default FabricanteProductoUpdate
